import React from 'react';
import { LinearProgress } from '@material-ui/core';
import Logo from '../assets/img/Olla - g128.png';

const messages = {
  home: "No cards! You can add items to OllaCart using chrome extension.",
  share: "Add items to your shared cart by clicking on the item once to turn the outline blue.",
  purchase: "Add items to your purchase cart by clicking them twice to turn the outline green."
}

const NoCard = ({page}) => {
  return (
    <div className="no-card-container">
      <img src={Logo} alt="logo"/>
      <div className="no-card-text">
        {
          messages[page]
        }
      </div>
      <LinearProgress />
    </div>
  );
};

export default NoCard;
